@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.btn {
  display: inline-block;
  background: #1d1d1d;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 30px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:hover {
  outline: none;
  opacity: .8;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}



.App {
}

.Title {
    width: 100vw;
    height: 100vh;
    z-index: 60;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.title-logo {
    max-width: 70%;
    max-height: 70%;
    overflow: hidden;
}


.btn-start {
    font-family: 'Acme';
    font-size: 3vw;
    margin: 20px;
    background-color: #0582c5;
    color: #d8202c;
}


.Game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.game-status-bar {
  background-color: white;
  box-shadow: 0px 5px 10px -6px #1d1d1d;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 100vw;
  position: fixed;
  z-index: 30;
  padding-top: 5px;
}

.time-container {
  width: 100px;
  height: 50px;
  border: 2px solid #d8202c;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 50px;
}

.game-logo {
  max-width: 100px;
  max-height: 100px;
  cursor: pointer;
}

.timer-container {
  width: 90px;
  height: 50px;
}

.characters-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 20%;
}

.character {
  width: 50px;
  height: 50px;
}

.img-face {
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
  border: 1px solid #0582c5;
}

.img-found {
  opacity: 0.2;
}

.game-image-container {
  position: relative;
  background-color: red;
  overflow: hidden;
  margin-top: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.game-image {
  /* box-shadow: 0px 5px 5px 5px lightgray; */
  max-width: 1024px;
  max-height: 768px;
  cursor: pointer;
}

/* Popup */
.Popup {
  width: 60px;
  height: 60px;
  border: 5px solid #0582c5;
  background-color: rgba(211, 211, 211, 0.39);
  z-index: 20;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #d8202c;
}

.Popup-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  background-color: #0582c5;
}

.btn-waldo,
.btn-odlaw,
.btn-wizard {
  background-color: #d8202c;
  height: 50px;
  width: 100px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.btn-face {
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
  border: 1px solid #0582c5;
  margin-right: 5px;
}
/* Find Again*/
.FindAgain {
  display: none;
  color: #d8202c;
  background: rgba(255, 255, 255, 0.541);
  border-radius: 20px;
  padding: 10px;
  z-index: 40;
  font-size: 30px;
  position: fixed;
}

/* Complete */
.Complete {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  z-index: 50;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0;
  background-color: #1d1d1d75;
}

.complete-container {
  border: 5px solid #0582c5;
  border-radius: 20px;
  padding: 40px;
  background-color: white;
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0px 5px 10px 1px #1d1d1d;
}

.msg-your-time {
  font-size: 36px;
  color: red;
}

.btn-play-again {
  margin-top: 20px;
  background-color: white;
  border: 3px solid #0582c5;
  color: #d8202c;
  padding: 5px;
  font-family: "Acme", Arial;
}

.Timer {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 90px;
    height: 50px;
}

#time {
    font-size: 20px;
    width: 200px;
}
.Leaderboard {
    width: 100vw;
    height: 100vh;
    z-index: 50;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow-y: scroll;
    overflow-x: scroll;
}

.msg-top-score {
    font-family: "Acme", Arial;
    font-size: 8vw;
    margin: 5px;
    margin-top: 10vh;
    color: #d8202c;
    text-shadow: 0 10px 1px #0582c5;
}

.Scores {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.msg-name, .msg-time {
    font-size: 20px;
    margin: 5px;
}

.msg-name {
    min-width: 10vw;
    max-width: 20vw;
    overflow: hidden;
}

.btn-top-replay {
    margin-top: 20px;
    background-color: white;
    border: 3px solid #0582c5;
    color: #d8202c;
    padding: 5px;
    font-family: "Acme", Arial;
}


