.Leaderboard {
    width: 100vw;
    height: 100vh;
    z-index: 50;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    overflow-y: scroll;
    overflow-x: scroll;
}

.msg-top-score {
    font-family: "Acme", Arial;
    font-size: 8vw;
    margin: 5px;
    margin-top: 10vh;
    color: #d8202c;
    text-shadow: 0 10px 1px #0582c5;
}

.Scores {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.msg-name, .msg-time {
    font-size: 20px;
    margin: 5px;
}

.msg-name {
    min-width: 10vw;
    max-width: 20vw;
    overflow: hidden;
}

.btn-top-replay {
    margin-top: 20px;
    background-color: white;
    border: 3px solid #0582c5;
    color: #d8202c;
    padding: 5px;
    font-family: "Acme", Arial;
}

